 .wp-block-loganduran-wgd-projects-gallery-block {
	color: #fff;
	padding: 2px;
	position: relative;
}

.slide {
	width: 100% ;
	height: 100%;
	opacity: 0;
	transition: opacity 1s ease-in-out;
	overflow: hidden;
	position: absolute;
	border-radius: inherit;
	img {
		width: 100%;
		height: 100%;
		border-radius: inherit;
		object-fit: cover;
		transform: scale(1);
		transition: transform 4s ease;
		position: absolute;
		top: 0;
		left: 0;
	}
	&.active {
		opacity: 1;
		img {
			transform: scale(1.03);
		}
	}
}